document.addEventListener("DOMContentLoaded", function() {
    function quantityProducts() {
      var quantityArrowsMinus = document.querySelectorAll(".quantity-arrow-minus");
      var quantityArrowsPlus = document.querySelectorAll(".quantity-arrow-plus");
  
      quantityArrowsMinus.forEach(function(quantityArrowMinus) {
        quantityArrowMinus.addEventListener("click", quantityMinus);
      });
  
      quantityArrowsPlus.forEach(function(quantityArrowPlus) {
        quantityArrowPlus.addEventListener("click", quantityPlus);
      });
  
      function quantityMinus() {
        var quantityNum = this.parentElement.querySelector(".quantity-num");
        if (parseInt(quantityNum.value) > 1) {
          quantityNum.value = parseInt(quantityNum.value) - 1;
        }
      }
  
      function quantityPlus() {
        var quantityNum = this.parentElement.querySelector(".quantity-num");
        quantityNum.value = parseInt(quantityNum.value) + 1;
      }
    }
  
    quantityProducts();
  });